import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { NgbdModalContent } from '../modals/bookListModal';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-listing',
  templateUrl: './listing.component.html',
  styleUrls: ['./listing.component.scss']
})
export class ListingComponent implements OnInit {
  active_language = 'english'
  classes = []
  pricelist ={}
  constructor(private modalService: NgbModal,public data:DataService) {
    this.updateClassesBasedOnMedium()
   }

  ngOnInit() {
    
  }
  changeMediumE(){
    this.active_language='english'
    this.updateClassesBasedOnMedium()


  }
  changeMediumH(){
    this.active_language='hindi'
    this.updateClassesBasedOnMedium()

  }
  updateClassesBasedOnMedium(){
    this.classes = this.data.medium[this.active_language]
    this.classes.forEach((classname) => {
      console.log(classname,this.data.classlist[classname])
      let price  = 0
      this.data.classlist[classname].books.forEach((book) =>{
        console.log(this.data.booklist[book].price)
        price +=  this.data.booklist[book].price
      })
      console.warn(price)
      this.pricelist[classname] = price;
    })
  }
  openModal(grade,where){
    console.log('open modal',grade)
      const modalRef = this.modalService.open(NgbdModalContent,
        {
          centered:true,
          windowClass:'book_list_modal',
          size:'lg',
        });
      modalRef.componentInstance.name = grade;
      this.data.all = where === 'buy'?true:false;
      this.data.activeClass = grade;
    }
  
}
