import { Component, OnInit, Input, ViewEncapsulation, AfterContentChecked, AfterViewInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { DataService } from '../services/data.service';

@Component({
    selector: 'ngbd-modal-content',
    templateUrl: `./bookListModal.html`,
    styleUrls:['./bookListModal.scss'],
    encapsulation: ViewEncapsulation.None

  })
  export class NgbdModalContent implements OnInit{
    @Input() name;
    selection=false;
    currentClass =null
    booklist =[]
    totalSelected = 0;
    totalPrice =0;
    modalName = ' class'
    constructor(public activeModal: NgbActiveModal,public data:DataService) {
      
    }
    ngOnInit(): void {
      //Called after the constructor, initializing input properties, and the first call to ngOnChanges.
      //Add 'implements OnInit' to the class.
      // console.log(this.data.activeClass)
      this.currentClass = this.data.activeClass
      this.modalName = this.data.classlist[this.data.activeClass].class
      // console.warn(this.modalName, this.data.classlist[this.data.activeClass])
      this.data.classlist[this.currentClass].books.forEach(book => {
        let temp = this.data.booklist[book]
        temp.selected =this.data.all;
        
        if(this.data.all){
          this.totalPrice += temp.price;
          this.totalSelected +=1;
        }
        else{
          this.totalPrice = 0
          this.totalSelected =0
        }
        this.booklist.push(this.data.booklist[book])
      });
      // console.log(this.booklist)

    }
    
    toggleSelection(i){
      let tc=0;
      let tcost=0;
      this.booklist[i].selected = !this.booklist[i].selected
      this.booklist.forEach(book => {
        if(book.selected){
          tc+=1;
          tcost+=book.price;
        }
      });
      this.totalSelected = tc;
      this.totalPrice = tcost;
    }
    addToCart(){
      let cartObj = {
        activeClass:this.data.activeClass,
        books:this.booklist.filter((book) => book.selected),
      }
      this.data.cartObject.push(cartObj)
      console.log(cartObj)
      this.activeModal.dismiss('cart')
    }
  }